import { BigNumber, Contract } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import CompoundLensJson from "../../../solidityInterfaces/CompoundLens.json";
import { CompoundLens } from "../../../solidityInterfaces/CompoundLens";
import {
  ICompoundLensService,
  TAccountLimits,
  TCTokenBalances,
  TCTokenMetadata,
  TCTokenUnderlyingPrice,
} from "./ICompoundLensService";
import { parseEther } from "ethers/lib/utils";
import { etheresStructResponseToObject } from "../contractsIntegrationUtils";

export class CompoundLensService implements ICompoundLensService {
  private compoundLensContract: CompoundLens;

  constructor(
    compoundLensAddress: string,
    signerOrProvider: Signer | Provider
  ) {
    this.compoundLensContract = new Contract(
      compoundLensAddress,
      CompoundLensJson.abi,
      signerOrProvider
    ) as CompoundLens;
  }

  async cTokenMetadata(cTokenAddress: string): Promise<TCTokenMetadata> {
    return etheresStructResponseToObject(
      await this.compoundLensContract.callStatic.cTokenMetadata(cTokenAddress)
    );
  }

  async cTokenMetadataAll(
    cTokenAddresses: string[]
  ): Promise<TCTokenMetadata[]> {
    const rawMetas = await this.compoundLensContract.callStatic.cTokenMetadataAll(
      cTokenAddresses
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async cTokenBalancesAll(
    cTokenAddresses: string[],
    account: string
  ): Promise<TCTokenBalances[]> {
    const rawMetas = await this.compoundLensContract.callStatic.cTokenBalancesAll(
      cTokenAddresses,
      account
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async cTokenUnderlyingPriceAll(
    cTokenAddresses: string[]
  ): Promise<TCTokenUnderlyingPrice[]> {
    const rawMetas = await this.compoundLensContract.callStatic.cTokenUnderlyingPriceAll(
      cTokenAddresses
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async getAccountLimits(
    comptrollerAddress: string,
    account: string
  ): Promise<TAccountLimits> {
    const rawAccountLimits = await this.compoundLensContract.getAccountLimits(
      comptrollerAddress,
      account
    );
    const [marketsIn, liquidity, shortfall] = rawAccountLimits;

    return {
      marketsIn,
      liquidity,
      shortfall,
    };
  }
}
