import { BigNumber, Contract, ContractTransaction } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import CEtherJson from "../../../solidityInterfaces/CEther.json";
import { ICEtherService } from "./ICEtherService";
import { CEther } from "../../../solidityInterfaces/CEther";

export class CEtherService implements ICEtherService {
  private cEther: CEther;

  constructor(erc20Address: string, signerOrProvider: Signer | Provider) {
    this.cEther = new Contract(
      erc20Address,
      CEtherJson.abi,
      signerOrProvider
    ) as CEther;
  }

  public async mint(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.mint({ value: amount });
  }

  public async withdraw(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.redeem(amount);
  }

  public async borrow(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.borrow(amount);
  }

  public async repayBorrow(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.repayBorrow({ value: amount });
  }
}
