import { BigNumber, Contract } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import TokensLensJson from "../../../solidityInterfaces/TokensLens.json";
import { ITokensLensService, TErc20Meta } from "./ITokensLensService";
import { UniswapAnchoredView } from "../../../solidityInterfaces/UniswapAnchoredView";
import { etheresStructResponseToObject } from "../contractsIntegrationUtils";
import { TokensLens } from "../../../solidityInterfaces/TokensLens";

export class TokensLensService implements ITokensLensService {
  private tokensLensContract: TokensLens;

  constructor(tokensLensAddress: string, signerOrProvider?: Signer | Provider) {
    this.tokensLensContract = new Contract(
      tokensLensAddress,
      TokensLensJson.abi,
      signerOrProvider
    ) as TokensLens;
  }

  async erc20MetadataAll(tokenAddresses: string[]): Promise<TErc20Meta[]> {
    const rawRes = await this.tokensLensContract.erc20MetadataAll(
      tokenAddresses
    );
    // const metaArray = rawRes.map()
    return rawRes;
  }

  async erc20MetadataAndBalanceAll(
    tokenAddresses: string[],
    ownerAddress: string
  ): Promise<[TErc20Meta[], BigNumber[]]> {
    const rawRes = await this.tokensLensContract.erc20MetadataAndBalanceAll(
      tokenAddresses,
      ownerAddress
    );

    const rawMetas = rawRes[0];
    const rawBalances = rawRes[1];

    return [rawMetas, rawBalances];
  }

  public async erc20AllowanceWithDecimals(
    owner: string,
    spender: string,
    tokenAddress: string
  ): Promise<[BigNumber, number]> {
    const [
      allowance,
      decimalsBN,
    ] = await this.tokensLensContract.erc20TokenAllowanceWithDecimals(
      tokenAddress,
      owner,
      spender
    );

    // Decimals are uint8;
    return [allowance, decimalsBN.toNumber()];
  }

  erc20Allowance(
    owner: string,
    spender: string,
    tokenAddress: string
  ): Promise<BigNumber> {
    return this.tokensLensContract.erc20TokenAllowance(
      tokenAddress,
      owner,
      spender
    );
  }

  erc20AllowanceAll(
    owner: string,
    spender: string,
    tokenAddresses: string[]
  ): Promise<BigNumber[]> {
    return this.tokensLensContract.erc20AllowancesAll(
      tokenAddresses,
      owner,
      spender
    );
  }

  erc20AllowancesMultiple(
    owner: string,
    spenders: string[],
    tokenAddresses: string[]
  ): Promise<BigNumber[]> {
    return this.tokensLensContract.erc20AllowancesMultiple(
      tokenAddresses,
      owner,
      spenders
    );
  }
}
